@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans+SC|Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.greeting,.header{
    font-size:46px;
    font-weight:bolder;
    font-family: 'Alegreya Sans SC', sans-serif;
    color:whitesmoke;
    /* -webkit-text-stroke: .1px black; */
    margin-bottom:0px;
}
.time{
    font-size: 40px;
}

.timer h2{
    font-size:46px;
    color:white;
}
.quote, .time, .author, label{
    color:whitesmoke;
    /* -webkit-text-stroke: .1px black; */
    font-family: 'Montserrat', sans-serif;
    font-weight:bolder;
}

.todo ul{
    color:whitesmoke;
font-size:18px;
    /* background-color:rgba(255, 255, 255,.5); */
}
.qod{
    margin-top:5%;
}
.App {
  text-align: center;
  margin:0px;
  padding:0px;
}
html{
  background-image: url(/IvanLanceDeLeon/productivity-app/static/media/bg3.466b3b8e.png);
  background-size:cover
}
.modules{
  display:flex;
  justify-content: space-evenly;
  vertical-align:top;
}
.timer{
  align-self:flex-start;
  

}

.toDo{
  align-self:flex-start;
}



